const App = () => {

  const [day, setDay] = useState(true);

  useEffect(() => {
    axios.get(`https://api.sunrise-sunset.org/json?lat=52.1326&lng=5.2913`)
      .then(res => {
        const data = res.data;
        if (data && data.results) {
          const sunrise = moment(data.results.sunrise, ['h:m a', 'H:m']);
          const sunset = moment(data.results.sunset, ['h:m a', 'H:m']);
          const now = moment();
          setDay(sunrise.diff(now) < 0 && sunset.diff(now) > 0);
        }
      })
  }, []);
  
  return (
    <ParallaxProvider>
        <div id='app' className={day ? '' : 'dark'}>
            <div></div>
            <div id='app-inner'>
                <Header/>
                <Info />
                <div id='vaardigheden' className='title'>Vaardigheden</div>
                <hr />
                <Vaardigheden />
                <div id='projecten' className='title'>Mijn projecten</div>
                <hr />
                {projects.map((project, key) => <Project project={project} key={key} />)}
                <div id='contact' className='title'>Contact</div>
                <hr />
                <Contact />
                <div id='footer'>
                    <FontAwesomeIcon icon={faCopyright}/>
                    {new Date().getFullYear()} Lucas Bos
                </div>
            </div>
            <div></div>
        </div>
    </ParallaxProvider>
  )
};

export default App;