import React from 'react';
import ScrollIntoView from "react-scroll-into-view";

const Header = () => {

    const toggleDropdown = () => {
        document.getElementById('header').classList.toggle('is-active');
        document.getElementById('menu').classList.toggle('is-active');
        document.getElementById('dropdown').classList.toggle('is-active');
    };

    const switchTheme = () => {
        document.getElementById('app').classList.toggle('dark');
    };

    const ScrollTo = ({ selector, children }) => (
        <ScrollIntoView 
            alignToTop={true} 
            selector={selector}    
        >
            {children}
        </ScrollIntoView>
    )

    return (
        <div>
            <div id='header'>
                <ScrollTo selector='#info'>Lucas Bos</ScrollTo>
                <ScrollTo selector='#vaardigheden'>Vaardigheden</ScrollTo>
                <ScrollTo selector='#projecten'>Projecten</ScrollTo>
                <ScrollTo selector='#footer'>Contact</ScrollTo>
                <div onClick={toggleDropdown} id='menu' className='hamburger hamburger--slider'>
                    <div className='hamburger-box'>
                        <div className='hamburger-inner'></div>
                    </div>
                </div>
                <div id='theme-switcher'>
                    <label className='switch'>
                        <input onClick={switchTheme} type='checkbox' />
                        <span className='slider round' />
                    </label>
                </div>
            </div>
            <div id='dropdown' onClick={toggleDropdown}>
                <ScrollTo selector='#vaardigheden'>Vaardigheden</ScrollTo>
                <ScrollTo selector='#projecten'>Projecten</ScrollTo>
                <ScrollTo selector='#footer'>Contact</ScrollTo>
            </div>
        </div>
    )
};

export default Header;